// Donnes de test avec BEAUCOUP de tches pour stress-test le layout grille 4 colonnes
export const massiveTestAppData = {
    Maison: {}, // L'onglet Maison n'aura pas de tches directement
    Adjointes: []
};

// Fonction pour gnrer des tches alatoirement
const generateRandomTasks = () => {
    const categories = [
        'Annulations',
        'Rclamations',
        'Validations',
        'Correspondances',
        'Expertises',
        'Sinistres',
        'Contrats',
        'Clients',
        'Facturations',
        'Juridique',
        'Techniques',
        'Archives'
    ];

    const taskPrefixes = [
        'Traitement', 'Validation', 'Vrification', 'Contrle', 'Analyse',
        'Suivi', 'Gestion', 'Rvision', 'Expertise', 'Inspection',
        'valuation', 'Approbation', 'Archivage', 'Transmission', 'Notification'
    ];

    const taskSuffixes = [
        'urgent', 'standard', 'prioritaire', 'diffr', 'spcial',
        'mensuel', 'hebdomadaire', 'quotidien', 'annuel', 'trimestriel',
        'automatique', 'manuel', 'numrique', 'papier', 'lectronique'
    ];

    const tasks = [];
    let taskId = 1;

    // Gnrer 15-25 tches par catgorie (total: ~240 tches)
    categories.forEach(category => {
        const tasksInCategory = Math.floor(Math.random() * 11) + 15; // 15-25 tches

        for (let i = 0; i < tasksInCategory; i++) {
            const prefix = taskPrefixes[Math.floor(Math.random() * taskPrefixes.length)];
            const suffix = taskSuffixes[Math.floor(Math.random() * taskSuffixes.length)];
            const modelNumber = Math.floor(Math.random() * 900) + 100; // 100-999

            const task = {
                id: taskId.toString(),
                name: `${prefix} ${suffix} ${category.toLowerCase()}`,
                category: category,
                fields: [
                    {
                        key: 'modelNumber',
                        label: 'Numro de Modle/Lettre',
                        type: 'text',
                        value: modelNumber.toString(),
                        adminOnlyEdit: true
                    },
                    {
                        key: 'fullTemplateName',
                        label: 'Modle de lettre',
                        type: 'text',
                        value: `${prefix} ${suffix} - Modle ${modelNumber}`,
                        adminOnlyEdit: true,
                        copyable: true
                    },
                    {
                        key: 'attachmentName',
                        label: 'Nom de Pice Jointe',
                        type: 'text',
                        value: `2025-06-26 ${category} - ${prefix} ${modelNumber}`,
                        adminOnlyEdit: false,
                        copyable: true
                    }
                ]
            };

            // Ajouter des champs supplmentaires alatoirement
            if (Math.random() > 0.5) {
                task.fields.push({
                    key: 'noteTemplate',
                    label: 'Note EPIC',
                    type: 'textarea',
                    value: `${category} - ${prefix} ${modelNumber} + documentation`,
                    adminOnlyEdit: false,
                    copyable: true,
                    specialButton: 'addDate'
                });
            }

            if (Math.random() > 0.7) {
                task.fields.push({
                    key: 'procedure',
                    label: 'Procdure  Suivre',
                    type: 'textarea',
                    value: `1. Analyser le dossier ${category.toLowerCase()}.\\n2. Appliquer la procdure ${modelNumber}.\\n3. Archiver le rsultat.`,
                    adminOnlyEdit: true,
                    hasCheckboxes: Math.random() > 0.5
                });
            }

            tasks.push(task);
            taskId++;
        }
    });

    return tasks;
};

// Gnrer les tches
massiveTestAppData.Adjointes = generateRandomTasks();

console.log(` Donnes de test gnres: ${massiveTestAppData.Adjointes.length} tches rparties sur ${new Set(massiveTestAppData.Adjointes.map(t => t.category)).size} catgories`);

// Stats par catgorie
const categoryStats = {};
massiveTestAppData.Adjointes.forEach(task => {
    categoryStats[task.category] = (categoryStats[task.category] || 0) + 1;
});

console.log(' Rpartition par catgorie:', categoryStats);
